import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import signin from '@/components/signin'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/signin',
      name: 'signin',
      component: signin
    }
  ]
})



