<template>
  <b-container>
    <b-jumbotron bg-variant="info" text-variant="white" border-variant="dark">
      <template slot="header">
        JerkHub
      </template>
      <template slot="lead">
        Jerkhub is a page that help you contract your personal health.
      </template>
      <b-btn @click="regUser">123</b-btn>
      <hr class="my-4">
    </b-jumbotron>
  </b-container>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      user: [],
      Reg: firebase.database().ref('reg')
    }
  },
  methods: {
    getData () {
      let vm = this
      this.$http.get('https://randomuser.me/api/?results=10')
        .then((r) => {
          vm.user = r.data.results
        })
        .catch((r) => {
          console.log(r)
        })
    },
    regUser () {
      let vm = this
      for (let obj of this.user) {
        firebase.auth().createUserWithEmailAndPassword(obj.email, obj.login.password)
          .then((e) => {
            console.log(e)
            console.log('註冊成功')
          })
          .catch((e) => {
            console.log(e)
            console.log('註冊失敗ＱＱ')
          })
      }
    }
  },
  mounted () {
    this.getData()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



