<template>
  <div id="app">
    <b-navbar toggleable="md" type="dark" variant="info">

      <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

      <b-navbar-brand href="#">NavBar</b-navbar-brand>
      <router-link :to="'/signin'">Sign In</router-link>

      <b-collapse is-nav id="nav_collapse">

        <b-navbar-nav>
          <b-nav-item><router-link :to="'/123'" class="text-white">123</router-link></b-nav-item>
          <b-nav-item href="#" disabled>Disabled</b-nav-item>
        </b-navbar-nav>

        <!-- Right aligned nav items -->
        <b-navbar-nav class="ml-auto">

          <b-nav-form>
            <b-form-input size="sm" class="mr-sm-2" type="text" placeholder="Search"/>
            <b-button size="sm" class="my-2 my-sm-0" type="submit">Search</b-button>
          </b-nav-form>
          <b-nav-item-dropdown right>
            <!-- Using button-content slot -->
              <template slot="button-content">
                <em>User</em>
              </template>
              <b-dropdown-item href="#">Profile</b-dropdown-item>
              <b-dropdown-item href="#">Signout</b-dropdown-item>
          </b-nav-item-dropdown>
        </b-navbar-nav>

      </b-collapse>
    </b-navbar>
    <b-row class="fixed-bottom">
      <b-btn v-b-tooltip title="123" v-b-toggle.collapse3 class="my-3 offset-10">TEST</b-btn>
      <b-collapse visible id="collapse3" class="my-3 offset-10">
        <b-card>
          <ul v-for="obj in test">
            <li>{{test}}</li>
          </ul>
          <b-input @keyup-enter="alert('test')"></b-input>
        </b-card>
      </b-collapse>
    </b-row>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      test: {123: 123, 456: 456, 789: 789}
    }
  }
}
</script>

<style>
a{
  text-decoration: none;
}
</style>



